.title {
    color: lightgrey;
    padding: 0.25rem;
    margin: 1em 1em;
    display: flex;
    flex-direction: row;
}

.underlined {
    border-bottom: 1px solid lightgrey;
}

#drop_down {
    min-width: 7.5rem;
    display: flex;
    flex-direction: row-reverse;
}

#card_profile {
    border-radius: 0.25rem;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    height: 15rem;
    width: 100%;
}

#card {
    border-radius: 0.25rem;
    width: 12rem;
    height: 20rem;
    margin: 0.5rem;
    position: relative;
}

#small_text {
    font-weight: normal;
}

.card_container_O {
    margin-bottom: 2rem;
}

.card_interact {
    width: 95;
    display: flex;
    flex-direction: row-reverse;
    padding: 1rem;
}

.card_container_I {
    width: 95%;
    max-height: 21rem;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    overflow-y: hidden;
}

.card_container_I_B {
    width: 95%;
    max-height: 63rem;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
}

#card_name {
    margin: auto;
    padding: 1rem;
}

#card_link {
    text-align: left;
}

#card_content {
    overflow: hidden;
}

#card_content h4 {
    margin-bottom: 0.2rem;
    margin-top: 0.2rem;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
}

#card_button {
    border-radius: 0.25rem;
    margin: 0.25rem;
    width: 6rem;
    text-align: center;
}

#card_button_hidden {
    margin: 0.25rem;
    width: 6rem;
    visibility: hidden;
}

.card_hidden {
    border-radius: 0.25rem;
    width: 12rem;
    height: 20rem;
    display: none;
    overflow-y: auto;
}

#card:hover {
    #card_profile {
        border-radius: 0.25rem;
        height: 20rem;
    }

    .card_hidden {
        display: flex;
        flex-direction: column;
        position: absolute;
        background-color: #353535c9;
    }
}
